export interface ErrorFormat {
    type: string;
    message: string;
}

export const getEditError = (type: string) => {
    const eArchive: Array<ErrorFormat> = [
        {type: 'invalidCityName', message: '     1 !'},
        {type: 'invalidAmountProportion', message: '      !'},
        {type: 'invalidExchangeVal', message: '      0!'},
        {type: 'badRequest', message: '  .'},
        {type: 'internalServerError', message: '   .'},
        {type: 'passDoesntMatch', message: '  !'},
        {type: 'invalidPassLength', message: '     6  30 .'},
        {type: 'invalidNewPassLength', message: '      6  30 .'},
        {type: 'invalidMessangerVal', message: '     2 !'},
        {type: 'failMessangerEdit', message: '   !'},
        {type: 'failPassEdit', message: '  !'},
        {type: 'wrongPass', message: '  !'}
    ];
    let err = eArchive.find(e => e.type === type);
    if (err) return err;
    else return null;
}